import Vue from 'vue'
import VueRouter from 'vue-router'
import App from '@/App.vue'
import routes from '@/routes'

import Hammer from 'hammerjs';
window.Hammer = Hammer.default;


Vue.use(VueRouter);

const router = new VueRouter({routes});

new Vue({
  el: '#app',
  render: h => h(App),
  router
})



